import { ErrorMessage, Field, Form, Formik } from "formik";
import * as React from "react";
import * as Yup from "yup";

const StepOne = (props) => {
  console.log("setpone", props);
  const handleSubmit = (values) => {
    console.log("handleSubmit", values);
    props.next(values, false);
  };

  const stepOneValidation = Yup.object({
    first_name: Yup.string().required("First Name"),
    last_name: Yup.string().required("First Name"),
    password: Yup.string().required("First Name"),
  });

  return (
    <Formik
      initialValues={props.data}
      validationSchema={stepOneValidation}
      onSubmit={handleSubmit}
    >
      {() => (
        <Form>
          <p>First</p>
          <Field name='first_name' />
          <ErrorMessage name='first_name' />
          <p>Last</p>
          <Field name='last_name' />
          <ErrorMessage name='last_name' />

          <p>Password</p>
          <Field name='Password' />
          <ErrorMessage name='password' />

          <button type='submit' onClick={handleSubmit}>
            NEXT
          </button>
        </Form>
      )}
    </Formik>
  );
};

export default StepOne;
